@import "../../assets/scss/app.scss";

.home {
  @include container;

  .inner {
    @include inner;

    .logo {
      @include onlyDesktop {
        width: 220px;
        display: block;
        margin: 0 auto;
        height: auto;
      }
    }

    .links {
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      .link {
        margin: 5px 10px;
        text-decoration: none;
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet {
          margin: 5px;
        }

        &:hover {
          img {

            @include tablet {
              border: 10px solid orange;
              transform: scale(1.03);
            }
          }

          p {
            color: #21231f;
            font-weight: bold;

            &:before {
              transform: translateX(0%);
            }
          }
        }

        img {
          width: 140px;
          border-radius: 10px;
          transition: .2s;

          @include tablet {
            border-radius: 25px;
            width: 200px;
            border: 10px solid transparent;
          }
        }

        p {
          //@include ModernistThree;
          width: 140px;
          margin-top: 4px;
          color: gold;
          font-size: 18px;
          text-align: center;
          position: relative;
          z-index: 2;
          transition: color 0.2s;
          overflow: hidden;

          @include tablet {
            width: 200px;
          }

          &::before {
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            content: "";
            display: block;
            background: gold;
            transform: translateX(-105%);
            transition: 0.2s;
          }
        }
      }
    }

  }
}