@import "../../../assets/scss/app.scss";

.item {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  transition: 0.3s;

  @include tablet {
    flex-direction: row;
  }

  &:not(:first-child) {
    margin-top: 16px;


  }

  .left {
    display: flex;
    flex-direction: row-reverse;

    @include tablet {
      flex-direction: row;
    }

    .date {
      background: #FFF;
      border-radius: 10px;

      flex-grow: 1;
      margin-left: 5px;

      height: 50px;
      padding: 0 10px;
      display: flex;
      align-items: center;
      transition: 0.3s;
      font-size: 16px;
      user-select: none;

      @include tablet {
        width: 150px;
        flex-grow: 0;
        margin-left: 0;
      }
    }

    .imgWrapper {
      flex-shrink: 0;
      height: 50px;
      width: 50px;
      border-radius: 10px;
      background: grey;
      overflow: hidden;
      position: relative;
      transition: 0.3s;

      @include tablet {
        margin-left: 10px;
      }

      .img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        transition: 0.3s;
      }

      .skeleton {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }

    }
  }

  .textBlock {
    margin-top: 5px;

    background: #FFF;
    border-radius: 10px;

    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    transition: 0.3s;

    @include tablet {
      margin-top: 0;
      flex-grow: 1;
      margin-left: 10px;
      height: 50px;
      padding: 0 10px;
    }

    p {
      user-select: none;
    }

    .text {
      font-size: 16px;
    }

    .fullText {
      font-size: 14px;
      margin-top: 10px;
      color: #555;
    }
  }

  &_selected {
    .left {
      .date {
        height: 200px;
        color: darkgreen;
        font-size: 18px;
      }

      .imgWrapper {
        height: 200px;
        width: 200px;
      }
    }

    .textBlock {
      @include tablet {
        height: 200px;
      }
    }
  }

}