@import "../../assets/scss/app.scss";

.burgerMenu {
  position: fixed;
  z-index: 777;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #2d2f29;
  @include centerColumn;
  transform: translateX(101%);
  transition: 0.3s;

  &_show {
    transform: translateX(0%);
  }

  .linksBlock {
    display: flex;
    flex-direction: column;
    align-items: center;


    &:not(:first-child) {
      margin-top: 16px;
    }

    .title {
      font-size: 18px;
      //text-decoration: underline;
      color: lime;
      font-weight: bold;
    }

    .links {
      display: flex;
      flex-direction: column;
      align-items: center;

      .link {
        text-decoration: none;
        color: orange;

        //&:not(:first-child) {
          margin-top: 8px;
        //}
      }
    }
  }



}