@import "../../../assets/scss/app.scss";

.byLocation {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .link {
    text-decoration: none;
    transition: 0.3s;
    margin: 5px;

    &:hover {
      .imgWrapper {
        border: 10px solid orange;
        transform: scale(1.02);
      }
      p {
        color: orange;
        //font-weight: bold;
      }
    }

    .imgWrapper {
      @include center();
      width: 100px;
      height: 100px;
      background: #000;
      border-radius: 12px;
      border: 6px solid #FFF;
      box-sizing: border-box;
      transition: 0.3s;
      padding: 6px;

      @include tablet {
        width: 140px;
        height: 140px;
        border-radius: 16px;
        border: 8px solid #FFF;
        padding: 8px;
      }

      @include desktop {
        width: 180px;
        height: 180px;
        border-radius: 20px;
        border: 10px solid #FFF;
        padding: 10px;
      }


      img {
        height: 100%;
        width: 100%;
      }


    }

    p {
      max-width: 100px;
      margin-top: 5px;
      font-size: 14px;
      text-align: center;
      transition: 0.3s;
      color: #FFF;

      @include tablet {
        max-width: 140px;
        margin-top: 5px;
        font-size: 18px;
      }

      @include desktop {
        max-width: 180px;
        margin-top: 5px;
        font-size: 24px;
      }
    }


  }
}