//======= BREACKPOINTS =========//
@mixin tablet {
  @media all and (min-width: 768px) {
    @content;
  }
}

@mixin desktop {
  @media all and (min-width: 1024px) {
    @content;
  }
}

@mixin onlyMobile($displayType: block) {
  display: $displayType;
  @content;

  @include desktop() {
    display: none;
  }
}

@mixin onlyTablet($displayType: block) {
  display: none;

  @include tablet {
    display: $displayType;
    @content;
  }
}

@mixin onlyDesktop($displayType: block) {
  display: none;

  @include desktop {
    display: $displayType;
    @content;
  }
}

//======= FONTS =========//
@mixin ModernistThree {
  font-family: 'Modernist Three';
}

@mixin KarnacOne {
  font-family: 'Karnac One';
}

@mixin h1() {
  @include ModernistThree;
  font-size: 36px;
  color: orange;

  @include desktop {
    font-size: 42px;
  }
}

@mixin h2() {
  font-size: 28px;
  color: orange;

  @include desktop {
    font-size: 32px;
  }
}

//======= OTHER =========//
@mixin container {
  position: relative;
  height: calc(100vh - 60px);
  overflow-y: auto;
}

@mixin inner {
  max-width: 1120px;
  width: 100%;
  min-height: 100%;
  padding: 16px;
  margin: 0 auto;
  box-sizing: border-box;
  background-color: rgba(33, 35, 31, 0.75);

  @include tablet {
    padding: 20px;
  }
}

@mixin center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@mixin button {
  border: none;
  background: none;
  cursor: pointer;
}

@mixin centerColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@mixin centeredColumn {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin centerRow {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin absoluteCenter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin absoluteStart {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}





