@import "../../assets/scss/app.scss";

.bricks {
  @include container;

  .inner {
    @include inner;
    display: flex;
    flex-direction: column;

    .titleBlock {
      align-self: center;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        @include h1();
        text-align: center;
        width: 340px;
      }

      .left, .right  {
        position: absolute;
      }

      .left {
        top: calc(50% + 25px);
        left: 100px;
        transform: translate(-100%, -50%);

        @include tablet {
          top: 50%;
          left: 30px;

        }

        @include desktop {
          left: 10px;
        }
      }

      .right {
        top: calc(50% + 25px);
        right: 100px;
        transform: translate(100%, -50%);

        @include tablet {
          top: 50%;
          right: 30px;
        }

        @include desktop {
          right: 10px;
        }
      }

      img {
        display: block;
        height: 50px;

        @include tablet {
          height: 75px;
        }

        @include desktop {
          height: 100px;
        }
      }
    }

    .sortMethod {
      margin-top: 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      @include tablet {
        justify-content: flex-start;
      }

      p {
        color: #FFF;
        font-size: 14px;

        @include desktop {
          font-size: 16px;
        }
      }

      .links {
        display: flex;

        a {
          text-decoration: none;

          .link {
            margin-left: 10px;
            font-size: 14px;
            color: gold;
            transition: 0.3s;

            @include tablet {
              margin-left: 20px;
              font-size: 14px;
            }

            @include desktop {
              margin-left: 30px;
              font-size: 16px;
            }

            &:hover {
              color: greenyellow;
            }

            &_active {
              color: greenyellow;
              text-decoration: underline;
            }
          }
        }


      }
    }

    .outlet {
      margin-top: 20px;
    }

  }
}