@import "../../../assets/scss/app.scss";

.links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-decoration: none;
  margin: -10px;

  .link {
    text-decoration: none;
    display: block;
    margin: 10px;

    .imgWrapper {
      width: 220px;
      padding: 10px;
      background: #FFF;
      box-sizing: border-box;
      border-radius: 20px;
      transition: 0.3s;

      img {
        width: 100%;
        display: block;
        border-radius: 10px;
      }
    }

    p {
      width: 220px;
      text-align: center;
      margin-top: 5px;
      color: #FFF;
      transition: 0.3s;
    }

    &:hover {
      .imgWrapper {
        background: orange;
      }

      p {
        color: orange;
      }
    }
  }
}
