@import "../../../assets/scss/app.scss";

.necroItem {

  .navigator {
    display: inline;

    a {
      color: lime;
      //text-decoration: none;
    }

    span {
      color: lime;
    }
  }

  .infoBlock {
    display: flex;
    flex-direction: column;
    margin-top: 16px;

    @include desktop {
      flex-direction: row;
    }

    img {
      max-width: 100%;
      height: auto;
      align-self: center;

      @include desktop {
        align-self: flex-start;
      }
    }

    .info {
      margin-top: 16px;

      @include desktop {
        margin-top: 0;
        margin-left: 32px;
      }

      .title {
        @include h2;
        text-align: center;

        @include desktop {
          text-align: left;
        }
      }

      .description {
        margin-top: 8px;
        color: #FFF;
        text-indent: 30px;
        text-align: justify;

        span {
          color: #faf089;
          font-style: italic;
        }
      }
    }
  }

  .sortBlock {
    margin-top: 16px;
    @include onlyMobile(flex);
    align-items: center;

    .btn {
      &:not(:first-child) {
        margin-left: 16px;
      }
    }
  }

  .table {
    margin-top: 4px;
    border: 1px solid #fff;
    border-radius: 5px;

    @include desktop {
      margin-top: 16px;
    }

    .head {
      @include onlyDesktop(flex);
      height: 30px;
      color: lime;
      background: rgba(33, 35, 31, 1);
      border-radius: 5px 5px 0 0;

      & > div {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 10px;
        box-sizing: border-box;

        &:not(:first-child) {
          border-left: 1px solid grey;
        }

        .btn {
          @include onlyDesktop(flex);
        }
      }

      .name {
        width: 300px;
      }

      .date {
        width: 110px;
      }

      .age {
        width: 110px;
      }

      .description {
        flex-grow: 1;
      }

      .photo {
        width: 60px;
      }

    }

    .body {

      .row {
        display: flex;
        flex-direction: column;
        border-top: 1px solid grey;
        color: #FFF;
        padding: 10px;
        background: rgba(33, 35, 31, .7);
        transition: 0.1s;
        position: relative;

        @include desktop {
          padding: 0;
          //height: 30px;
          flex-direction: row;
        }

        &:hover {
          background: rgba(33, 35, 31, .3);
        }

        & > div {

          .grey {
            @include onlyMobile(inline);
            color: grey;
            //width: 80px;
          }

          @include desktop {
            display: flex;
            align-items: center;
            box-sizing: border-box;
            padding: 5px 10px;
          }

          &:not(:first-child) {
            @include desktop {
              border-left: 1px solid grey;
            }
          }
        }

        .name {
          @include desktop {
            width: 300px;
            flex-shrink: 0;
          }

        }

        .date {
          @include desktop {
            width: 110px;
            flex-shrink: 0;
          }
        }

        .age {
          @include desktop {
            width: 110px;
            flex-shrink: 0;
          }
        }

        .description {
          @include desktop {
            flex-grow: 1;
          }
        }

        .photo {
          position: absolute;
          bottom: 10px;
          right: 10px;

          @include desktop {
            flex-shrink: 0;
            position: relative;
            bottom: auto;
            right: auto;
            width: 60px;
            @include centerRow;
          }

          .showSliderBtn {
            @include button;
            @include centerRow;
            color: #FFF;
            transition: 0.3s;

            &:hover {
              color: lime;
            }
          }


        }
      }
    }



  }
}