@import "../../../assets/scss/app.scss";

.bricksLocation {

  .nameWrapper {
    display: flex;
    align-items: center;
    justify-content: center;

    .imgWrapper {
      width: 50px;
      height: 50px;
      box-sizing: border-box;
      padding: 5px;
      border-radius: 10px;
      border: 4px solid orange;
      overflow: hidden;
      background: #000;
      display: flex;
      justify-content: center;
      align-items: center;

      //@include tablet {
      //  width: 50px;
      //  height: 50px;
      //  border: 4px solid orange;
      //  border-radius: 10px;
      //}
      //
      //@include desktop {
      //  border-radius: 10px;
      //  width: 50px;
      //  height: 50px;
      //  border: 4px solid orange;
      //}

      img {
        display: block;
        width: 100%;
        height: auto;
      }
    }

    .name {
      margin-left: 10px;
      color: #FFF;
    }
  }

  .markCards {
    .card {
      margin-top: 40px;
    }
  }
}