@import "../../../assets/scss/app.scss";

.incDecButton {
  @include button;
  display: flex;
  align-items: center;

  p {
    @include onlyMobile();
    color: grey;
    margin-right: 4px;
  }

  svg {
    transition: 0.3s;

    path {
      transition: 0.3s;
      fill: grey;
    }
  }

  &_selected {
    p {
      color: lime;
    }

    svg path {
      fill: orange;
    }
  }

  &_dec {
    svg {
      transform: rotate(180deg);
    }
  }
}