@import "../../../assets/scss/app";

.slider {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background: rgba(0, 0, 0, 0.75);
  @include centerColumn;

  .closeBtn {
    @include button;
    position: absolute;
    z-index: 111;
    top: 10px;
    right: 10px;

  }

  .swiperWrapper {
    position: relative;
    width: 100%;
    height: 100%;

    .swiper {
      width: 100%;
      height: 100%;

      .slide {
        @include centerRow;

        img {
          display: block;
          max-height: 533px;
          max-width: 100%;
          height: auto;
        }
      }
    }
  }

  .prevBtn, .nextBtn {
    position: absolute;
    z-index: 111;
    top: 50%;
    transform: translateY(-50%);

    svg {
      width: 24px;
      height: 24px;

      @include desktop {
        width: 80px;
        height: 80px;
      }
    }
  }

  .prevBtn {
    left: 0;
  }

  .nextBtn {
    right: 0;
  }
}