@import "../../../assets/scss/app.scss";

.sourceIndex {
  padding: 20px;

  .title {
    @include h1();
    text-align: center;
  }

  .subtitle {
    @include h2;
    margin-top: 24px;
    text-align: center;
  }

  .links {
    margin-top: 16px;
    display: flex;
    flex-direction: column;

    .link {
      text-decoration: none;
      color: #FFF;
      margin-top: 6px;

      &:hover {
        text-decoration: underline;
      }
    }
  }
}