@import "../../assets/scss/app.scss";

.book {
  @include container;

  .inner {
    @include inner;

    .title {
      @include h1();
      text-align: center;
    }

    .sortBlock {
      margin-top: 5px;
      padding: 20px 0;

      display: flex;
      justify-content: space-between;
      align-items: center;

      .text {
        color: #FFF;
        font-size: 14px;

        @include tablet {
          font-size: 16px;
        }

      }

      .sortButtons {
        display: flex;

        .btn {
          cursor: pointer;
          color: #FFF;
          transition: 0.3s;
          user-select: none;
          font-size: 14px;

          @include tablet {
            font-size: 16px;
          }


          &:hover {
            color: lime;
          }

          &_selected {
            color: lime;
            text-decoration: underline;
          }


          &:not(:first-child) {
            margin-left: 8px;

            @include tablet {
              margin-left: 20px;
            }
          }
        }
      }

    }

    .items {
      margin-top: 5px;

      .item {
        &:not(:first-child) {
          margin-top: 16px;
        }
      }
    }
  }
}